'use client';

import { useState, useEffect, Suspense } from 'react';
import { useSearchParams } from 'next/navigation';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Check, Star, PartyPopper } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { usePageBackground } from '@/hooks/usePageBackground';
import { Badge } from '@/components/ui/badge';
import { useRouter } from 'next/navigation';
import { Skeleton } from '@/components/ui/skeleton';
import { useToast } from '@/hooks/use-toast';

const RazorpaySubscriptionButton = ({ userId }: { userId: string }) => {
  useEffect(() => {
    const rzpForm = document.getElementById('razorpay-form');
    if (!rzpForm || !userId) return;

    if (rzpForm.firstChild) {
      rzpForm.removeChild(rzpForm.firstChild);
    }
    
    const script = document.createElement('script');
    script.src = 'https://cdn.razorpay.com/static/widget/subscription-button.js';
    script.async = true;
    script.setAttribute('data-subscription_button_id', 'pl_Ohp287a2N1d5gR');
    script.setAttribute('data-button_theme', 'brand-color');
    
    // Pass the Firebase UID in the notes
    const notes = {
      firebase_uid: userId,
    };
    script.setAttribute('data-notes', JSON.stringify(notes));
    
    rzpForm.appendChild(script);

  }, [userId]);

  return (
    <form id="razorpay-form" className="w-full flex justify-center"></form>
  );
};


function PricingContent() {
  usePageBackground('');
  const { user, profile, isLoading, upgradeToPro } = useAuth();
  const router = useRouter();
  const searchParams = useSearchParams();
  const { toast } = useToast();
  const [isUpgrading, setIsUpgrading] = useState(false);
  
  const isAlreadyPro = profile?.subscriptionStatus === 'pro';

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
    }
  }, [isLoading, user, router]);

  useEffect(() => {
    const subscriptionId = searchParams.get('razorpay_subscription_id');
    if (subscriptionId && !isAlreadyPro && !isUpgrading) {
      setIsUpgrading(true);
      toast({
        title: 'Payment Successful!',
        description: 'Verifying and upgrading your account to Pro...',
      });
      // The webhook will handle the upgrade. We just need to give it a moment
      // and then refresh the user's profile state.
      setTimeout(() => {
          upgradeToPro().then(() => {
              toast({
                title: 'Welcome to Pro!',
                description: 'Your account has been successfully upgraded.',
              });
              router.replace('/pricing', { scroll: false });
          }).catch(err => {
              console.error("Upgrade check failed", err);
              toast({
                title: 'Upgrade Pending',
                description: 'We are processing your upgrade. Please refresh in a moment.',
                variant: 'destructive',
              });
          }).finally(() => {
              setIsUpgrading(false);
          });
      }, 5000); // Wait 5 seconds for webhook to process
    }
  }, [searchParams, isAlreadyPro, upgradeToPro, toast, router, isUpgrading]);


  if (isLoading || !user || !profile) {
    return (
        <div className="max-w-4xl mx-auto">
            <div className="text-center mb-12">
                <Skeleton className="h-12 w-3/4 mx-auto" />
                <Skeleton className="h-6 w-1/2 mx-auto mt-4" />
            </div>
            <div className="flex justify-center px-4 sm:px-0">
                <Skeleton className="h-[500px] w-full max-w-md" />
            </div>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold tracking-tight text-foreground font-headline sm:text-5xl">
          {isAlreadyPro ? "Welcome to the Pro Club!" : "Unlock Your Full Potential"}
        </h1>
        <p className="mt-4 max-w-2xl mx-auto text-lg text-muted-foreground">
          {isAlreadyPro ? "You have access to all features. Keep up the great work!" : "Choose the plan that's right for you and start mastering mental math today."}
        </p>
      </div>
      
      <div className="flex justify-center px-4 sm:px-0">
        <Card className="w-full max-w-md shadow-2xl border-2 border-primary md:scale-105">
           <CardHeader className="text-center bg-primary/10 p-6">
                {isAlreadyPro ? <PartyPopper className="w-12 h-12 text-primary mx-auto mb-4" /> : <Star className="w-12 h-12 text-primary mx-auto mb-4" />}
                <CardTitle className="text-3xl font-bold">Abacus Pro</CardTitle>
                <CardDescription className="text-lg">
                    {isAlreadyPro ? "Your access to premium features is active." : "Everything you need to become a math whiz."}
                </CardDescription>
                {!isAlreadyPro && (
                  <div className="mt-4 flex flex-col items-center gap-2">
                    <div className="flex items-baseline gap-4">
                      <p className="text-2xl font-semibold text-muted-foreground line-through">₹799</p>
                      <Badge variant="destructive" className="text-base h-fit py-1 px-3 -rotate-12 transform">55% OFF</Badge>
                    </div>
                    <p className="text-5xl font-extrabold tracking-tight text-foreground">₹360<span className="text-base font-medium text-muted-foreground">/month</span></p>
                  </div>
                )}
           </CardHeader>
           <CardContent className="p-6 md:p-8">
                <ul className="space-y-4">
                    {includedFeatures.map((feature) => (
                        <li key={feature} className="flex items-center gap-3">
                            <Check className="h-6 w-6 text-green-500" />
                            <span className="text-muted-foreground">{feature}</span>
                        </li>
                    ))}
                </ul>
           </CardContent>
           <CardFooter>
             {isAlreadyPro ? (
                <div className="w-full text-center text-primary font-semibold text-lg flex items-center justify-center gap-2">
                  <Check className="w-6 h-6"/> You are a Pro member!
                </div>
             ) : (
                <div className="w-full flex justify-center">
                  <RazorpaySubscriptionButton userId={user.uid} />
                </div>
             )}
           </CardFooter>
        </Card>
      </div>
    </div>
  )
}

export default function PricingPage() {
    return (
        <Suspense fallback={<div>Loading...</div>}>
            <PricingContent />
        </Suspense>
    );
}

const includedFeatures = [
  'Unlimited Practice Tests',
  'All Difficulty Levels',
  'Detailed Progress Tracking',
  'No Advertisements',
  'Priority Support',
];
